#main1 {
  background-color: #4186bb;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  font-family: "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #ffffff;
}

.app {
  background-color: #252d4a;
  width: 450px;
  min-height: 200px;
  height: min-content;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
  display: inline-block;
  justify-content: space-evenly;
}

.score-section {
  /* display: flex; */
  font-size: 24px;
  align-items: center;
}

/* QUESTION/TIMER/LEFT SECTION */
.question-section {
  width: 100%;
  position: relative;
}

.question-count {
  margin-bottom: 20px;
}

.question-count span {
  font-size: 28px;
}

.question-text {
  margin-bottom: 35px;
}

.timer-text {
  background: rgb(230, 153, 12);
  padding: 15px;
  margin-top: 20px;
  margin-right: 20px;
  border: 5px solid rgb(255, 189, 67);
  border-radius: 15px;
  text-align: center;
}

/* ANSWERS/RIGHT SECTION */
.answer-section {
  width: 100%;
  flex-direction: column;
  justify-content: space-between;
}

.a_btn {
  width: 100%;
  align-items: center;
  font-size: 16px;
  color: #ffffff;
  background-color: #252d4a;
  border-radius: 15px;
  display: flex;
  padding: 5px;
  justify-content: center;
  cursor: pointer;
  white-space: normal;
  word-wrap: break-word;
}

.incorrect {
  width: 100%;
  align-items: center;
  font-size: 16px;
  color: #ffffff;
  background-color: #ff3333;
  border-radius: 15px;
  display: flex;
  padding: 5px;
  justify-content: center;
  cursor: pointer;
  white-space: normal;
  word-wrap: break-word;
}
.correct {
  width: 100%;
  align-items: center;
  font-size: 16px;
  color: #ffffff;
  background-color: #2f922f;
  border-radius: 15px;
  display: flex;
  padding: 5px;
  justify-content: center;
  cursor: pointer;
  white-space: normal;
  word-wrap: break-word;
}

.a_btn:hover {
  background-color: #555e7d;
}

.a_btn:focus {
  outline: none;
}
