:root {
  --max-width: 1160px;
  --border-radius: 8px;
  --background: #fff;
  --background-gray: #f9fbfc;
  --text-white: #fff;
  --text-dark: #0e1c34;
  --text-gray-light: #b1afb9;
  --text-gray: #6d7278;
  --progbr-color: #1f95fd;
  --grad-blue-medium: linear-gradient(225deg, #29678b 0%, #0e1c34 100%);
  --grad-blue: linear-gradient(
    225deg,
    rgb(30, 79, 108) 0%,
    rgb(14, 28, 52) 100%
  );
  --grad-blue-light: linear-gradient(29.83deg, #1f95fd 0%, #4fe8e4 100%);
  --grad-orange: linear-gradient(
    225deg,
    #ffaf00 0%,
    #fe4d1b 48.15%,
    #e70154 100%
  );
  --box-shadow-card: 10px 10px 25px 1px #c0c5ce;
  --box-shadow-blue: 0 18px 40px 0 rgba(79, 232, 228, 0.5);
  --box-shadow-orange: 0 18px 40px 0 rgba(252, 71, 31, 0.3);
  --box-shadow-orange-dark: 0 20px 60px 10px rgba(252, 71, 31, 0.6);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#title {
  margin-bottom: -10px;
}

.btn {
  display: inline-flex;
  -moz-box-align: center;
  align-items: center;
  align-self: center;
  white-space: nowrap;
  min-height: 64px;
  padding: 18px 20px;
  margin: 10px;
  background: var(--grad-orange);
  border-radius: var(--border-radius);
  transition: transform 0.3s ease 0s;
  font-size: 16px;
  font-weight: bold;
  line-height: 22px;
  color: var(--text-white);
  cursor: pointer;
  border: medium none;
}
